import React from "react";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { Dropdown } from "react-bootstrap";
import { ShopWindow } from "react-bootstrap-icons";

const PharmacyNav = ({ setDrawer }) => {
	const navigate = useNavigate();
	const inst = useSelector((state) => state.inst.activeInst);
	return (
		<>
			<Dropdown style={{ width: "100%" }}>
				<Dropdown.Toggle className="nav-link" variant="transparent">
					<ShopWindow /> Pharmacy
				</Dropdown.Toggle>
				<Dropdown.Menu variant="dark">
					<Dropdown.Item>
						<p
							className="nav-link"
							onClick={() => {
								window.scroll(0, 0);
								setDrawer(false);
								navigate(`pharmacy-categories`);
							}}
						>
							Pharmacy Categories
						</p>
					</Dropdown.Item>
					<Dropdown.Item>
						<p
							className="nav-link"
							onClick={() => {
								window.scroll(0, 0);
								setDrawer(false);
								navigate(`pharmacy-products`);
							}}
						>
							Pharmacy Products
						</p>
					</Dropdown.Item>
					<Dropdown.Item>
						<p
							className="nav-link"
							onClick={() => {
								window.scroll(0, 0);
								setDrawer(false);
								navigate(`pharmacy-entries`);
							}}
						>
							Pharmacy Entries
						</p>
					</Dropdown.Item>{" "}
					<Dropdown.Item>
						<p
							className="nav-link"
							onClick={() => {
								window.scroll(0, 0);
								setDrawer(false);
								navigate(`pos`);
							}}
						>
							Point Of Sale
						</p>
					</Dropdown.Item>{" "}
				</Dropdown.Menu>
			</Dropdown>
			<hr />
		</>
	);
};

export default PharmacyNav;
