import React from "react";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { Dropdown } from "react-bootstrap";
import { CashCoin, Shop } from "react-bootstrap-icons";

const StoreNav = () => {
	const navigate = useNavigate();
	const inst = useSelector((state) => state.inst.activeInst);
	return (
		<>
			<Dropdown style={{ width: "100%" }}>
				<Dropdown.Toggle className="nav-link" variant="transparent">
					<Shop /> Store
				</Dropdown.Toggle>
				<Dropdown.Menu variant="dark">
					<Dropdown.Item>
						<p
							className="nav-link"
							onClick={() => {
								window.scroll(0, 0);
								navigate(`store-categories`);
							}}
						>
							Store Categories
						</p>
					</Dropdown.Item>
					<Dropdown.Item>
						<p
							className="nav-link"
							onClick={() => {
								window.scroll(0, 0);
								navigate(`store-items`);
							}}
						>
							Store Items
						</p>
					</Dropdown.Item>
					<Dropdown.Item>
						<p
							className="nav-link"
							onClick={() => {
								window.scroll(0, 0);
								navigate(`store-entries`);
							}}
						>
							Store Entries
						</p>
					</Dropdown.Item>{" "}
				</Dropdown.Menu>
			</Dropdown>
			<hr />
		</>
	);
};

export default StoreNav;
