import React, { useEffect } from "react";
import { socket } from "./SockectConnect";
import { useSelector, useDispatch } from "react-redux";
import { FetchPatients, FetchStaffs } from "../Network/CredApi";
import { FetchModes } from "../Network/ModeApi";
import { FetchExpenseCats } from "../Network/ExpenseCatApi";
import { FetchExpenses } from "../Network/ExpenseApi";
import { FetchAppointments } from "../Network/AppointmentApi";
import { FetchBeds } from "../Network/BedApi";
import { FetchEntries } from "../Network/EntryApi";
import { FetchHealths } from "../Network/HealthApi";
import { FetchInsComps } from "../Network/InsCompApi";
import { FetchInvoices } from "../Network/InvoiceApi";
import { FetchPayments } from "../Network/PaymentApi";
import { FetchPharmacyCats } from "../Network/PharmacyCatApi";
import { FetchProducts } from "../Network/ProductApi";
import { FetchServices } from "../Network/ServiceApi";
import { FetchServiceCats } from "../Network/ServiceCatApi";
import { FetchStoreCats } from "../Network/StoreCatApi";
import { FetchStoreEntries } from "../Network/StoreEntryApi";
import { FetchItems } from "../Network/ItemApi";
import { FetchWards } from "../Network/WardApi";
import { FetchSubs } from "../Network/SubApi";
import { FetchVitals } from "../Network/VitalApi";
import { FetchMpesaPayments } from "../Network/MpesaApi";
import { FetchNotifications } from "../Network/NotificationApi";
import { FetchMessages } from "../Network/MessageApi";
import { FetchPayrollCats } from "../Network/PayrollCatApi";
import { FetchPayrollEntries } from "../Network/PayrollEntryApi";
import { FetchPayrollItems } from "../Network/PayrollItemApi";

const SocketMessages = () => {
	//redux dispatch
	const dispatch = useDispatch();

	const User = useSelector((state) => state.auth.user);
	const online = useSelector((state) => state.online.online);
	const ActiveCred = useSelector((state) => state.cred.activeCred);

	const inst = useSelector((state) => state.inst.activeInst);

	useEffect(() => {
		socket.on("message", async (message) => {
			UpdateMessage(message);
		});
	}, []);

	const UpdateMessage = (message) => {
		if (message.messageType === "notification") {
			if ("serviceWorker" in navigator && "PushManager" in window) {
				navigator.serviceWorker.ready
					.then((registration) => {
						registration.showNotification(message.dataValues.title, {
							body: message.dataValues.note,
							icon: "",
						});
					})
					.catch((err) => console.log(err));
			}
			FetchNotifications(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "staff") {
			FetchStaffs(
				ActiveCred.instLinker,
				User.token,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "mode") {
			FetchModes(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}

		if (message.messageType === "expenseCat") {
			FetchExpenseCats(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "expense") {
			FetchExpenses(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "appointment") {
			FetchAppointments(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "bed") {
			FetchBeds(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "entry") {
			FetchEntries(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "health") {
			FetchHealths(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "vital") {
			FetchVitals(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "insComp") {
			FetchInsComps(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "invoice") {
			FetchInvoices(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "patient") {
			FetchPatients(
				ActiveCred.instLinker,
				User.token,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "payment") {
			FetchPayments(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "online-payment") {
			FetchMpesaPayments(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "pharmacyCat") {
			FetchPharmacyCats(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "product") {
			FetchProducts(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "service") {
			FetchServices(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "serviceCat") {
			FetchServiceCats(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "storeCat") {
			FetchStoreCats(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "storeEntry") {
			FetchStoreEntries(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "storeItem") {
			FetchItems(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "ward") {
			FetchWards(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "sms") {
			FetchMessages(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "payrollCat") {
			FetchPayrollCats(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "payrollItem") {
			FetchPayrollItems(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "payrollEntry") {
			FetchPayrollEntries(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
		if (message.messageType === "sub") {
			FetchSubs(
				User.token,
				ActiveCred.instLinker,
				dispatch,
				online,
				"socket",
				message.dataValues
			);
		}
	};

	return <></>;
};

export default SocketMessages;
